import styled from 'styled-components';
import theme from './theme';


const Link = styled.a`
  font-weight: ${theme.fw1};
  color: ${theme.primary};
  position: relative;
  text-decoration: none;

  &:before{
    content: " ";
    position:absolute;
    width:100%;
    height:2px;
    bottom:0;
    left:0;
    background-color: ${theme.white};
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out;
  }
  &:hover{
    &:before{
      visibility: visible;
      transform: scaleX(1);
    }
  }



`;

export default Link;